.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
}
.page__body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  color: #283645;
  background-color: #ffffff;
  min-width: 320px;
}
img {
  width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  position: relative;
  z-index: 1;
  margin-bottom: -70px;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 660px) {
  .page-header {
    margin-bottom: -120px;
  }
}
@media (min-width: 1200px) {
  .page-header {
    margin-bottom: -150px;
  }
}
.main-nav {
  position: relative;
}
.main-nav__header {
  padding: 20px;
  box-sizing: border-box;
}
.main-nav__logo {
  margin: 0;
  padding: 0;
  width: 74px;
  height: 23px;
}
.main-nav__logo-link {
  display: block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
}
.main-nav__logo-img:hover {
  fill: #D22856;
}
.main-nav__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
}
.main-nav__button::after,
.main-nav__button::before {
  content: '';
  position: absolute;
  transform: translateX(-50%);
  top: 0;
  background-color: white;
}
.main-nav__button:hover::before,
.main-nav__button:hover::after {
  background-color: #d22856;
}
.main-nav__button:active::before,
.main-nav__button:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.button-open {
  width: 50px;
  height: 24px;
}
.button-open::after {
  width: 50px;
  height: 4px;
  box-shadow: 0px 10px 0px 0px white,
      0 20px 0 0 white;
}
.button-open:hover::after {
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.button-open:active::after {
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
.button-close {
  display: none;
  width: 24px;
  height: 24px;
}
.button-close::after,
.button-close::before {
  top: 11px;
  left: 0;
  width: 25px;
  height: 4px;
  background-color: white;
}
.button-close::after {
  transform: rotate(45deg);
}
.button-close::before {
  transform: rotate(135deg);
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: none;
}
.main-nav__item {
  position: relative;
  width: 100%;
  box-shadow: 0px -1px 0 0 rgba(0, 0, 0, 0.7), 0px 1px 0 0 rgba(0, 0, 0, 0.7);
}
.main-nav__link {
  display: block;
  padding: 20px;
  width: 100%;
  box-sizing: border-box;
  background-color: #283645;
  color: white;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 32px;
  text-align: center;
  font-family: "Open Sans Bold", Arial, sans-serif;
  font-weight: 100;
}
@media (max-width: 1199px) {
  .main-nav--active-menu .main-nav__list {
    display: block;
  }
  .main-nav--active-menu .button-close {
    display: block;
  }
  .main-nav--active-menu .button-open {
    display: none;
  }
  .main-nav--active-menu .main-nav__header {
    position: relative;
    background-color: #283645;
  }
  .main-nav--no-js .main-nav__list {
    display: block;
  }
  .main-nav--no-js .button-open {
    display: none;
  }
  .main-nav--no-js .main-nav__header {
    background-color: #283645;
  }
}
@media (min-width: 660px) {
